import React from 'react'
import { useQuery, gql } from '@apollo/client';

const Login = () => {
    const LOGIN_USER = gql`
        mutation {
            login(email: "alan0803789627@outlook.com", password: "bob12345") {
                path
                message
            }
        }
  `
    const { loading, data, error } = useQuery(LOGIN_USER)
    console.log(error)
    console.log(loading)
    console.log(data)
    return (
    <div>login</div>
  )
}

export default Login